#auth {
    height: 100vh;
    overflow-x: hidden
}

#auth #auth-right {
    background: url(../../assets/images/bg.jpg), linear-gradient(90deg, #2d499d, #3f5491);
    height: 100%
}

#auth #auth-left {
    padding: 5rem 8rem
}

#auth #auth-left .auth-title {
    font-size: 4rem;
    margin-bottom: 1rem
}

#auth #auth-left .auth-subtitle {
    color: #a8aebb;
    font-size: 1.7rem;
    line-height: 2.5rem
}

#auth #auth-left .auth-logo {
    margin-bottom: 7rem
}

#auth #auth-left .auth-logo img {
    height: 2rem
}

@media screen and (max-width:767px) {
    #auth #auth-left {
        padding: 5rem
    }
}

.login-logo {
    height: 100px;
    margin-bottom: 30px;
    /* width: 100px; */
}